.page {
  font-family: "Inter", sans-serif;
  scroll-behavior: smooth;

  &__body--with-menu {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform #{$transitionTime};

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;
  }

  &__section-title {
    margin-bottom: 48px;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    color: #{$colorFont};

    @include on744 {
      margin-bottom: 64px;
      font-size: 40px;
      line-height: 40px;
    }

    @include on1260 {
      margin-bottom: 64px;
      font-size: 48px;
      line-height: 48px;
    }
  }
}
